.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.key {
  /* some styles for the key */
  height: 60px;
  width: 35px;
  border: 2px solid white;
  padding: 4px;
  color: black;
}

.key.enter {
  width: 60px;
}

.key.backspace {
  width: 60px;
}

.gameboard {
  padding: 10px;
}

.gameboard-row {
  display: flex;
  justify-content: center;
  height: 60px;
  line-height: 50px;
}

.letter {
  color: black;
  background-color: lightgray;
  width: 60px;
  border: 2px solid white;
  font-size: 25px;
}

.green {
  color: white;
  background-color: #538d4e;
}

.yellow {
  color: white;
  background-color: #b59f3b;
}

.gray {
  color: white;
  background-color: #787c7e;
}

.button {
  height: 40px;
  padding-left:10px;
  padding-right:10px;
  padding-top:10px;
}

.button-text {
  height: 40px;
  padding-left:10px;
  padding-right:10px;
}

.wordInput {
  height: 35px;
  text-align: center;
}

.wordPickerText {
  padding:10px;  
}

@keyframes shake {
  0% { transform: translate(1px, 1px) rotate(0deg); }
  10% { transform: translate(-1px, -2px) rotate(-1deg); }
  20% { transform: translate(-3px, 0px) rotate(1deg); }
  30% { transform: translate(3px, 2px) rotate(0deg); }
  40% { transform: translate(1px, -1px) rotate(1deg); }
  50% { transform: translate(-1px, 2px) rotate(-1deg); }
  60% { transform: translate(-3px, 1px) rotate(0deg); }
  70% { transform: translate(3px, 1px) rotate(-1deg); }
  80% { transform: translate(-1px, -1px) rotate(1deg); }
  90% { transform: translate(1px, 2px) rotate(0deg); }
  100% { transform: translate(1px, -2px) rotate(-1deg); }
}

.shake {
  animation: shake 0.5s;
  animation-iteration-count: 1;
}

.game-over-modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
}

.game-over-modal-content {
  position: relative;
  background-color: white;
  padding: 20px;
  border-radius: 15px; /* Increase border-radius for more curved corners */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.close-modal {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
}

.custom-button {
  display: block;
  margin-bottom: 10px;
  padding: 10px 20px;
  background-color: #538d4e;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s, color 0.3s;
}

.custom-button:hover {
  background-color: darkgreen;
  color: white;
}

.button-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top:15px;
}
